$font: 'Roboto', Arial,Helvetica,sans-serif;
$color1: #A2CC01;

* {
  box-sizing: border-box;
}

body {
  background: #fff;
  font: 400 14px/14px $font;
}

p {
  margin: 0;
  padding: 0;
}

a,
a:hover {
  text-decoration: none;
}

.content {
  width: 980px;
  margin: 0 auto;
}

.main {
  background: url("/dist/img/bg_main.png") no-repeat center center;
  min-height: 100vh;
  display: flex;

  div {
    width: 45%;
  }

  .desk {
    margin-top: 150px;
    width: 55%;
    font: 300 41px/1.3em $font;
    color: $color1;
    text-align: center;
    letter-spacing: 1px;
  }
}

.about {
  margin-top: 100px;
  display: flex;

  .desk {
    width: 60%;
    font: 400 19px/1.3em $font;
    color: rgba(136,135,133,1);

    h2 {
      margin-bottom: 40px;
      color: $color1;
      font: 300 33px/1.3em $font;
      letter-spacing: 1px;
    }
  }

  .img {
    width: 40%;
  }
}

.contact {
  margin-top: 100px;

  h2 {
    margin-bottom: 40px;
    color: $color1;
    font: 300 33px/1.3em $font;
    letter-spacing: 1px;
  }

  .fl {
    display: flex;
    flex-wrap: wrap;

    div {
      margin: 20px 0 30px;
      &:nth-child(1) {
        width: 40%;
      p {
        font: 400 16px/1.3em $font;
        white-space: pre-line;
      }

      }

      &:nth-child(2) {
        width: 60%;
        display: flex;
        margin-top: -10px;

        button {
          border: 0;
    color: #FFFFFF;
    cursor: pointer;
    background-color: rgba(255, 159, 36, 1);
    color:#fff;
    font:300 19px/1.3em  $font;
          position: absolute;
          top:110px;
          right:180px;
        }
        > div {
          &:nth-child(1) {
            display: flex;
            flex-direction: column;


            input {
              min-height: 32px;
              margin: 1px 2px;
            }
          }

          &:nth-child(2) {
                    margin-top: 20px;
                        position: relative;
            input {
              max-height: 100px;
            }
          }
        }

        input {
          border: 1px solid #ff9f24;
          background: #fff;
          color: #888785;
          font: 400 16px/1.3em $font;
        }
      }
    }
  }
}

.header {
  display: flex;
  align-items: center;

  a {
    margin: 0 20px;
    font: 300 18px/1.1em $font;
    color: #000;

    &:hover {
      color: $color1;
    }
  }

  .logo {
    margin-right: 60px;
    size: 145px 132px;
    background: url("/dist/img/logo.png") no-repeat;
  }
}

.footer {
  margin: 50px 0 10px;
  font: 400 14px $font;
  color: #000;
}
